.alert {
  margin-top: 60px;
  padding: 160px 0;
  font-size: 2.5rem;
  font-family: bungee;
  color: #ff7c90;
  display: none;
  text-align: center;

  a:hover {
    color: rgb(119, 119, 209) !important;
  }
  a {
    color: rgb(137, 184, 238);
    font-size: 1.8rem;
  }
}
.alert-active {
  display: block !important;
}
.cartcontainer-active {
  display: none;
}
.cartcontainer {
  width: 1200px;
  margin: 140px auto 60px;
  border: 2px solid #333;
  box-shadow: 8px 8px #969696;
  display: block;
  h1 {
    height: auto;
    font-size: 25px;
    font-family: var(--main-font);
    margin: 0;
    border-bottom: 2px solid #333;
    padding: 4px 30px;
  }
}
.product-item-cart {
  height: auto;
  border-bottom: 2px solid #333;
  padding: 30px 80px;
  display: flex;
  justify-content: space-between;
  line-height: 20px;
  img {
    width: 76px;
    height: 76px;
    border: 2px solid #333;
  }
  .product-list-right {
    display: flex;
    flex: 1;
    margin-left: 50px;
    justify-content: space-between;

    .product-detail {
      font-family: Cascadia Code;
      font-size: 1.6rem;
      width: 230px;
      .product-detail-title {
        margin-bottom: 10px;
        font-weight: 700;
      }
    }
    .classsify {
      .classify-title {
        font-weight: 700;
        font-size: 1.6rem;
        margin-bottom: 10px;
      }
    }
    .count {
      display: flex;
      margin: 20px 0;

      .minus-active {
        background-color: rgb(212 204 204) !important;
        cursor: none;
      }
      .minus,
      .flus {
        width: 40px;
        height: 40px;
        border: 2px solid #333;
        text-align: center;
        line-height: 32px;
        cursor: pointer;
        &:hover {
          background-color: yellow;
          transition: all ease-in-out 0.6s;
        }
      }
      .count-number {
        width: 60px;
        text-align: center;
        height: 40px;
        border: 2px solid #333;
        border-left: none;
        border-right: none;
        text-align: center;
        line-height: 32px;
      }
    }
    .delete {
      cursor: pointer;
      width: 40px;
      height: 40px;
      border: 2px solid #333;
      margin-top: 20px;
      text-align: center;
      line-height: 30px;
      font-weight: 700;
      font-size: 20px;
      &:hover {
        background-color: yellow;
        transition: all ease-out 0.5s;
      }
    }
    .cart-price {
      font-size: 16px;
      font-weight: 600;
      margin-top: 30px;
      font-family: bungee;
      width: 100px;
    }
  }
}
.payment {
  padding: 15px 80px;
  background-color: pink;
  display: flex;
  align-items: center;
  justify-content: space-between;
  p {
    font-size: 2.5rem;
  }
  .payment-price {
    display: flex;
    align-items: center;
    p {
      font-family: bungee;
      font-size: 20px;
      margin-right: 20px;
    }
    .button {
      margin-top: 0 !important;
      width: 120px;
      height: 40px;
      line-height: 30px;
    }
  }
}

@media screen and (max-width: 391px) {
  .cartcontainer {
    margin: 120px 20px 80px 20px;
    width: 350px;
    h1 {
      font-size: 2rem;
    }

    li {
      padding-left: 10px;
      .classsify {
        display: flex;
        margin-top: 10px;
      }
      .product-list-right {
        margin-left: 30px;
        display: block;
        position: relative;

        .count {
          .flus,
          .minus {
            width: 35px;
            height: 35px;
          }
          .count-number {
            height: 35px;
          }
        }
        .cart-price {
          margin-top: 10px;
          width: 200px;
          font-size: 1.6rem;
        }
        .delete {
          width: 35px;
          height: 35px;
          position: absolute;
          bottom: -10px;
          right: 20px;
        }
      }
    }
    .payment {
      padding: 10px;
      p {
        font-size: 2.1rem;
      }
      .payment-price {
        display: block;
        margin-top: 15px;

        p {
          font-size: 1.5rem;
        }
        button {
          width: 100px;
        }
      }
    }
  }
}
