.widget {
  width: 100%;
  height: 590px;
  display: flex;

  .widget-left {
    flex: 1.5;
    border: 1px solid gray;

    img {
      border-bottom: 1px solid gray;

      height: 70%;
      width: 100%;
    }
    .content {
      height: 30%;
      padding: 42px 78px;
      font-size: 18px;
      font-family: "Courier New", monospace;
    }
  }
  .widget-right {
    flex: 1;

    width: 100%;
    .widget-list {
      padding-right: 20%;
      height: 100%;
      border: 1px solid gray;

      .don {
        background-color: #ff7c90 !important;
      }
      .lin {
        background-color: #e3a97e !important;
      }
      .an {
        background-color: #bcdaff !important;
      }
      .chom {
        background-color: #ffd682 !important;
      }

      .widget-item {
        display: flex;
        height: 22%;
        margin-bottom: 20px;
        align-items: center;
        border: 2px solid gray;
        box-shadow: 7px 7px gray;

        img {
          width: 129px;
          height: 129px;
        }
        p {
          font-size: 4.5rem;
          font-family: Bungee;
          margin-left: 10px;
          font-weight: 700;
        }
      }
    }
  }
}
@media screen and (max-width: 391px) {
  .widget {
    display: block;
    height: unset;
    .widget-left {
      .content {
        padding: 30px 12px 0 12px;
        height: 190px;
      }
    }
    .widget-right {
      .widget-list {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        padding: 0;

        li {
          width: 100% !important;
          height: 65px !important;
          box-shadow: none !important;
          margin-bottom: 0 !important;
          border: 1px solid #ccc !important;
          img {
            width: 30px !important;
            height: 30px !important;
          }
          p {
            font-size: 17px !important;
            margin-left: 0 !important;
          }
        }
      }
    }
  }
}
